.listImg {
  width: 100px;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.list {
  list-style: none;
}

.listItem {
  border: 1px solid white;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  align-items: center;
  width: 1080px;
}

.listItem:hover,
.listItem:focus {
  background-color: green;
  color: ecru;
}

.listName {
  font-size: 22px;
  font-weight: 700;
  margin: 15px 150px;
  width: 300px;
}

.listCharacter {
  font-size: 22px;
}

.listWrap {
  width: 1200px;
  height: 500px;
  overflow: overlay;
  border: 1px solid white;
  border-radius: 10px;
  box-shadow: rgb(0 0 0 / 50%) 0px 0px 10px;
}

.error {
  font-size: 30px;
  color: grey;
  margin-left: 150px;
  margin-top: 40px;
}

.loading {
  display: flex;
  width: 100%;
  position: relative;
  transition: 0.5s;
  top: 0;
  left: 0;
}